'use strict';

Admin.Modules.register('form.elements.datetime', function () {
    $('.input-date').each(function (i, item) {
        var $self = $(item);

        $self.datetimepicker({
            locale: Admin.locale
        }).on('dp.change', function () {
            $self.change();
        });
    });
});


