"use strict";

module.exports = function () {

    return {

        /**
         * Вывод сообщения об ошибке
         *
         * @param {String} title заголовок
         * @param {String} message текст
         * @returns {*}
         */

        error: function error(title, message) {
            return this.message(title, message, "error");
        },


        /**
         * Вывод Success сообщения
         * @param {String} title заголовок
         * @param {String} message текст
         * @returns {*}
         */
        success: function success(title, message) {
            return this.message(title, message, "success");
        },


        /**
         * Вывод сообщения
         *
         * @param {String} title заголовок
         * @param {String} message текст
         * @param {String} type Тип сообщения (error, success)
         * @returns {*}
         */
        message: function message(title, _message, type) {
            return swal(title, _message, type || 'success');
        },


        /**
         * Вывод сообщения с подтверждением
         *
         * @param {String} title заголовок
         * @param {String} message текст
         * @param {Function} callback Код выполняемый при подтверждении
         */
        confirm: function confirm(title, message) {
            return swal({
                title: title,
                text: message || '',
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3c8dbc',
                cancelButtonColor: '#d33',
                confirmButtonText: trans('lang.button.yes')
            });
        },


        /**
         * Вывод сообщения с полем ввода
         *
         * @param {String} title
         * @param {String} message
         * @param {Function} callback Код выполняемый при подтверждении
         * @param {String} inputPlaceholder Вспомогательный текст для поля ввода
         */
        prompt: function prompt(title, message, inputPlaceholder) {
            return swal({
                title: title,
                text: message || '',
                input: 'text',
                showCancelButton: true,
                closeOnConfirm: false,
                inputPlaceholder: inputPlaceholder || ''
            });
        }
    };
}();


