'use strict';

Admin.Modules.register('form.elements.select', function () {
    $('.input-select').each(function (e, item) {
        var options = {},
            $self = $(item);

        if ($self.hasClass('input-taggable')) {
            options['tags'] = true;
        }

        $self.select2(options);
    });
}, 0, ['bootstrap::tab::shown']);


