'use strict';

Admin.Modules.register('display.columns.checkbox', function () {
    $('.adminCheckboxRow').on('change', function (e) {
        var $self = $(e.target),
            $row = $self.closest('tr');

        if ($self.is(':checked')) {
            $row.addClass('info');
        } else {
            $row.removeClass('info');
        }
    });

    $('.adminCheckboxAll').on('change', function (e) {
        var $self = $(e.target),
            $checkboxes = $('.adminCheckboxRow');

        if ($self.is(':checked')) {
            $checkboxes.not(':checked').each(function (i, item) {
                item.checked = true;
                $(item).trigger('change');
            });
        } else {
            $checkboxes.filter(':checked').each(function (i, item) {
                item.checked = false;
                $(item).trigger('change');
            });
        }
    });
});


