'use strict';

Admin.Modules.register('form.elements.daterange', function () {
    $('.input-daterange').each(function (i, item) {
        var $self = $(item);

        var picker = $self.daterangepicker({
            showDropdowns: true,
            locale: {
                format: $self.data('format'),
                separator: '::'
            }
        });

        if ($self.data('startdate')) {
            $self.data('daterangepicker').setStartDate($self.data('startdate'));
        }

        if ($self.data('enddate')) {
            $self.data('daterangepicker').setEndDate($self.data('enddate'));
        }
    });
});


