'use strict';

Admin.Modules.register('display.datatables', function () {
    $.fn.dataTable.ext.errMode = function (dt) {
        Admin.Messages.error(dt.jqXHR.responseJSON.message || trans('lang.table.error'));
    };

    $.fn.dataTable.ext.order['DateTime'] = function (settings, col) {
        return this.api().column(col, { order: 'index' }).nodes().map(function (td, i) {
            return $(td).data('value');
        });
    };

    function iterateColumnFilters(datatableId, callback) {
        $('[data-datatables-id="' + datatableId + '"] .column-filter[data-type]').each(function (i, subitem) {
            var $element = $(subitem);

            callback($element, $element.closest('[data-index]').data('index'), $element.data('type'));
        });
    }

    $('.datatables').each(function (i, item) {
        var $this = $(item),
            id = $this.data('id'),
            params = $this.data('attributes') || {},
            url = $this.data('url');

        if (url && url.length > 0) {
            params.serverSide = true;
            params.processing = true;
            params.ajax = {
                url: url,
                data: function data(d) {
                    Admin.Events.fire('datatables::ajax::data', d);

                    iterateColumnFilters(id, function ($element, index, type) {
                        if (name = $element.data('ajax-data-name')) {
                            d.columns[index]['search'][name] = $element.val();
                        }
                    });
                }
            };
        }

        params.fnDrawCallback = function (oSettings) {
            Admin.Events.fire('datatables::draw', this);
        };

        var table = $this.DataTable(params);

        iterateColumnFilters(id, function ($element, index, type) {
            if (_.isFunction(window.columnFilters[type])) {
                window.columnFilters[type]($element, table.api(), table.api().column(index), index, params.serverSide);
            }
        });
    });
});

window.checkNumberRange = function (fromValue, toValue, value) {
    if (_.isNaN(fromValue) && _.isNaN(toValue)) {
        return true;
    }

    if (_.isNaN(value)) {
        return false;
    }

    if (_.isNaN(fromValue) && value <= toValue) {
        return true;
    }

    if (_.isNaN(toValue) && value >= fromValue) {
        return true;
    }

    return value >= fromValue && value <= toValue;
};

window.checkDateRange = function (fromValue, toValue, value) {
    if (!_.isObject(fromValue) && !_.isObject(toValue)) {
        return true;
    }

    if (!value.isValid()) {
        return false;
    }

    if (!_.isObject(fromValue) && value.isSameOrBefore(toValue)) {
        return true;
    }

    if (!_.isObject(toValue) && value.isSameOrAfter(fromValue)) {
        return true;
    }

    return value.isBetween(fromValue, toValue);
};

window.columnFilters = {
    daterange: function daterange(dateField, table, column, index, serverSide) {
        var $dateField = $(dateField);

        $dateField.on('apply.daterangepicker', function (e, date) {
            column.search($dateField.val()).draw();
        });
    },
    range: function range(container, table, column, index, serverSide) {
        var $container = $(container),
            from = $('input:first', $container),
            to = $('input:last', $container),
            isDateRange = false;

        from.data('ajax-data-name', 'from');
        to.data('ajax-data-name', 'to');

        from.add(to).on('keyup change', function () {
            if (serverSide) {
                column.search(from.val() + '::' + to.val()).draw();
            } else {
                table.draw();
            }
        });

        if (from.closest('.input-date').length > 0 && to.closest('.input-date').length > 0) {
            from.closest('.input-date').add(to.closest('.input-date')).on('dp.change', function () {
                if (serverSide) {
                    column.search(from.val() + '::' + to.val()).draw();
                } else {
                    table.draw();
                }
            });

            isDateRange = true;
        }

        if (serverSide) {
            return;
        }

        $.fn.dataTable.ext.search.push(function (settings, data, dataIndex) {
            if (table.settings()[0].sTableId != settings.sTableId) {
                return true;
            }

            var value = data[index];

            if (value && !_.isUndefined(value['@data-order'])) {
                value = value['@data-order'];
            }

            if (isDateRange) {
                return checkDateRange(from.val().length > 0 && from.closest('.input-date').data('DateTimePicker').date(), to.val().length > 0 && to.closest('.input-date').data('DateTimePicker').date(), moment(value, from.data('date-format')));
            }

            return checkNumberRange(parseInt(from.val()), parseInt(to.val()), parseInt(value));
        });
    },
    select: function select(input, table, column, index, serverSide) {
        var $input = $(input);

        $input.on('change', function () {
            var selected = [];
            $input.find(':selected').each(function (i, e) {
                var $option = $(e);

                if ($option.val().length) {
                    selected.push($option.text());
                }
            });

            if (serverSide) {
                column.search(selected.join(',')).draw();
            } else {
                column.search(selected.join('|'), true, false, true).draw();
            }
        });
    },
    text: function text(input, table, column, index, serverSide) {
        var $input = $(input);

        $input.on('keyup change', function () {
            column.search($input.val()).draw();
        });
    }
};


