'use strict';

Admin.Modules.register('display.columns.control', function () {
    var clickEvent = function clickEvent(selector, question) {
        $('table').on('click', selector, function (e) {
            e.preventDefault();

            var $form = $(this).closest('form');

            Admin.Messages.confirm(question).then(function () {
                $form.submit();
            }, function (dismiss) {});
        });
    };

    clickEvent('button.btn-delete', trans('lang.table.delete-confirm'));
    clickEvent('button.btn-destroy', trans('lang.table.destroy-confirm'));
});


