'use strict';

Vue.component('element-image', Vue.extend({
    props: {
        url: {
            required: true
        },
        value: {
            default: ''
        },
        file: {
            default: ''
        },
        readonly: {
            type: Boolean,
            default: false
        },
        name: {
            type: String,
            required: true
        }
    },
    data: function data() {
        return {
            errors: []
        };
    },
    ready: function ready() {
        this.initUpload();
    },

    methods: {
        initUpload: function initUpload() {
            var self = this,
                container = $(self.$el.parentNode),
                button = container.find('.upload-button');

            button.dropzone({
                url: this.url,
                method: 'POST',
                uploadMultiple: false,
                previewsContainer: false,
                acceptedFiles: 'image/*',
                dictDefaultMessage: '',
                sending: function sending() {
                    self.closeAlert();
                },
                success: function success(file, response) {
                    self.value = response.value;
                    self.file = response.path;
                },
                error: function error(file, response) {
                    if (_.isArray(response.errors)) {
                        self.$set('errors', response.errors);
                    }
                }
            });
        },
        remove: function remove() {
            var self = this;

            Admin.Messages.confirm(trans('lang.message.are_you_sure')).then(function () {
                self.value = '';
                self.file = '';
            });
        },
        closeAlert: function closeAlert() {
            this.$set('errors', []);
        }
    },
    computed: {
        has_value: function has_value() {
            return this.value.length > 0;
        },
        image: function image() {
            return this.file.indexOf('http') === 0 ? this.file : Admin.Url.upload(this.file);
            // return ((this.value.indexOf('http') === 0) ? this.value : Admin.Url.upload(this.value))
        }
    }
}));


